.vendorsPannelFooter{height:10%;background:white;box-shadow:0 0 15px #ccc;display:flex;flex-flow:row nowrap;align-items:center;justify-content:space-evenly}.vendorsPannelFooter.hidden{position:relative;display:none}.vendorsPannelFooter>div{height:50px;width:50px;border-radius:50%;display:flex;align-items:center;justify-content:center}.vendorsPannelFooter>div:last-child{background:#1EBE71}

