@import '../registration.sharableStyles.scss';
@import '../../../../../assets/globalStyles.scss';

.businessInfoFormContainer {
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    overflow-x: auto;
    background: white;
    padding: $padding;

    & button {
        @include greenButton();
    }

    &>div {
        width: Min(100%, 500px);
        margin: auto;

        .businessInfoForm {
            .businessType {
                display: flex;
                flex-flow: row nowrap;
                align-items: center;
                justify-content: space-between;
                color: $brown;
                font-weight: bold;

                &>p.active {
                    outline: 2px solid $brown;
                    padding: 1%;
                }
            }

            .inputInfo {

                & label {
                    text-align: left;
                    display: block;
                    font-weight: bold;
                    margin-left: Min(4%, 10px);
                }

                & input {
                    @include input();
                }

                & select {
                    @include input();
                    font-size: .9em;
                    font-weight: bold;

                    &>option {
                        @include input();
                        font-size: .9em;
                        font-weight: bold;
                    }
                }

                .firstNameInputContainer {
                    display: flex;
                    flex-flow: row wrap;
                    align-items: center;
                    justify-content: space-between;

                    .businessTitle {
                        width: 22%;
                    }

                    .businessFirstName {
                        width: 75%;
                    }
                }
            }

            .multipleInputs {
                width: 100%;
                display: flex;
                flex-flow: row wrap;
                align-items: center;
                justify-content: space-between;

                .halfWidth {
                    width: 48%;
                }
            }

            & .businessInfo {
                margin-top: 15px;
            }
        }

        .uploadDocuments {
            margin: 20px 0;

            &>label {
                display: flex;
                align-items: center;
                justify-content: flex-start;
            }

            &>p {
                font-size: .7em;
                color: darken(#ccc, 20%);
            }

            &>input {
                display: none;
            }
        }

        .businessInfoFormHeader {
            color: $green;
            text-align: left;
            margin-bottom: 10px;
        }
    }
}