@import '../../../../assets/globalStyles.scss';

$circleSize:45px;

.vendorsPannelHeader {
    padding: 0 2%;
    height: 10%;
    background-color: $darkGreen;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    color: white;

    &.hidden {
        display: none;
    }

    .addSignBoard {
        font-size: .8em;
        display: flex;
        flex-flow: column nowrap;
        align-items: center;

        .addSignBoardIcon {
            border: 1px solid lighten(black, 40%);
            border-radius: 50%;
            height: $circleSize;
            width: $circleSize;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    .vendorsName {
        flex-grow: 1;
        font-size: 1.3em;
        text-align: left;
        padding-left: 2%;
        font-weight: bold;
        overflow-x: hidden;
        overflow-y: auto;
    }
}